@import url(https://fonts.googleapis.com/css2?family=Crimson+Pro&family=Literata);
body {
  margin: 0;
  padding: 0;
  background-color: #17141d;
  display: flex;
  flex-direction: column;
  position: relative;
  align-content: center;
  font-family: 'Literata',monospace;
  font-size:1rem;
  justify-content: center;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.recepie{ 
    display: flex;
    position: relative;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
    height: auto;
    width: auto;
    margin: .5rem;
    min-width: auto;
    /* margin: 1rem; */
    padding: .5rem;
    border-radius: 10px;
    /* border-color: rgb(156, 165, 165); */
    background-color: rgb(96, 128, 128);
    box-shadow: -1rem 0 3rem #000;
    transition: .1s;
    /* border-spacing: 10px; */
    
}

.card-header{
    font-size: larger;
    font-family: cursive;
    font-weight: 600;
    margin: 1rem;
    transition: .3s;
}
.card-header:hover{
    color: black;
    text-shadow: 2px 2px 5px orangered;
    /* background: linear-gradient(90deg,#ff8a00,#e52ee5);
    text-shadow: none;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color:transparent; */
}

.img{
    /* max-width: 200px;
    min-width: 50px;
    max-height: 350px;
    min-height: 100px; */
    display: block;
    width: 200px;
    height: 200px;
    transition: .2s;
    border-radius: 25%;
}
.img :hover{
   transform: translateX(2rem);
   transform:translateY(2rem);
}
.ingrediants{
    display: inline;
    text-align: start;
}

.recepie:hover{
    transform: translateY(-1rem);
}
.recepie:hover~.recepie{
    transform: translateX(130px);
}
.recepie:not(:first-child){
    margin-left:-130px;
}

